import React from 'react'
import { useHistory } from 'react-router-dom'

import Typography from '@material-ui/core/Typography'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Button from '@material-ui/core/Button'
import { withStyles } from '@material-ui/core/styles'

import CustomEnhancedTable from "../CustomEnhancedTable/CustomEnhancedTable";
import {DateFormatterByAbbreviation} from "../../../../../Utilities/TextFormatter";
import moment from "moment";

// Styles
import classes from './BillingAnalystTab.module.scss'

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.grey,
    color: theme.palette.common.white
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover
    }
  }
}))(TableRow)

const FooterBar = props => {
  const { cancelAssigning, fetchData } = props
  const history = useHistory()

  const handleOnClickCancel = () => {
    if (history.location.hash.replace(/[#/]/g, '') === 'from_modal') {
      history.push('/#Client-Ledger-Setup')
    } else {
      // history.push('/')
      cancelAssigning()
      fetchData()
    }
  }

  const handleOnClose = () => {
    history.push('/')
  } 

  return (
    <div className={classes.button}>
      <Button variant='contained' className={classes['button-negative']} onClick={handleOnClickCancel}>
        Cancel
      </Button>
      <Button variant='contained' className={classes['button-positive']} onClick={handleOnClose}>
        Close
      </Button>
    </div>
  )
}

export default function BillingAnalystTab (props) {
  const { reset, cancelReset, cancelAssigning, headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore, pagination, params, fetchData, changeView } = props

  return (
    <>
    <div style={{display: 'inline-block', width: '200px'}}>
      <p><span>BILLING ANALYST</span></p>
      <p><span>STATUS</span></p>
      <p><span>NO. OF ACCOUNTS</span></p>
    </div>
    <div style={{display: 'inline-block'}}>
      <p><strong>{localStorage.getItem('full_name')}</strong></p>
      <p><strong>Active</strong></p>
      <p><strong>{users.count}</strong></p>
    </div>

      <CustomEnhancedTable
        count={users.count}
        changeView={changeView}
        tableHeaders={headCells}
        pagination={pagination}
        search
        sort
        fetchData={fetchData}
        params={params}
        reset={reset}
        cancelReset={cancelReset}
        noSearchIcon={true}
      >
        <TableBody>
          {users.body
            ? users.body.map((item, index) => (
              <StyledTableRow key={index}>
                <StyledTableCell component='th' id={`analyst-number-${index}`}>
                  <Typography variant='caption' align='left'>{item.client.account_number}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-name-${index}`}>
                  <Typography variant='caption' align='left'>{item.client.account_name}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-contract-type-${index}`}>
                  <Typography variant='caption' align='left'>{item.contract.type}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-contract-type-${index}`}>
                  <Typography variant='caption' align='left'>{DateFormatterByAbbreviation(item.contract.effectivity)}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-contract-type-${index}`}>
                  <Typography variant='caption' align='left'>{DateFormatterByAbbreviation(item.contract.expiration)}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-payment-term-${index}`}>
                  <Typography variant='caption' align='left'>{item.contract.payment_term}</Typography>
                </StyledTableCell>
                <StyledTableCell component='th' id={`analyst-billing-cycle-${index}`}>
                  <Typography variant='caption' align='left'>{item.billing_code_cycle}</Typography>
                </StyledTableCell>
              </StyledTableRow>
            ))
            : 'No Data'}
        </TableBody>
      </CustomEnhancedTable>
      <FooterBar cancelAssigning={cancelAssigning} fetchData={fetchData}/>
    </>
  )
}
