import React from 'react'
import Button from '@material-ui/core/Button'
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormGroup from '@material-ui/core/FormGroup';
import FormLabel from '@material-ui/core/FormLabel'
import Popover from '@material-ui/core/Popover'
import Checkbox from '@material-ui/core/Checkbox';

import noop from 'lodash/noop'
import moment from 'moment'

import DatePicker from '../../../Components/FormComponents/DatePicker/DatePicker'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'

import RBAC from 'Security/RBAC'

// Style
import classes from './../ListOfAccountsPage.module.scss'

const theme = createMuiTheme({
  overrides: {
    // Style sheet name ⚛️
    MuiPopover: {
      // Name of the rule
      paper: {
        // Some CSS
      }
    }
  }
})

export default function DisplaySettings(props) {
  const {
    from = "AccountListPage",
    displaySettingTitle = 'Effectivity Date Range',
    handleOnDisplaySettingsApplyClicked = noop,
    handleDisplaySettingsClicked = noop,
    onChange = noop,
    startDateError = 'Please enter a valid effectivity start date',
    endDateError = 'Please enter a valid effectivity end date',
    laterStartDateError = 'Please select an effectivity start date that is not later than the current date',
    laterEndDateError = 'Please select an effectivity end date that is not later than the current date',
    startDateGreaterThanEndDateError = 'Please select an effectivity start date that is not later than the end date'
  } = props

  const [values, setValues] = React.useState({
    start: null,
    end: null
  })
  const [debounce, setDebounce] = React.useState(null)
  const [anchorEl, setAnchorEl] = React.useState()
  const [toDateError, setToDateError] = React.useState('')
  const [fromDateError, setFromDateError] = React.useState('')
  const [selectedStatus, setSelectedStatus] = React.useState('active')
  const [selectedDisplayType, setSelectedDisplayType] = React.useState('scrollable')
  const [displayColumns, setDisplayColumns] = React.useState({
    billing_code : true,
    member_name : true,
    member_type : true,
    effectivity : true,
    ticket_number : true,
    transaction_status : true,
    billing_type : true,
    soa_number : true,
    claim_number : true,
    transaction_date : true,
    member_id : true,
    plan_type : true,
    cancelled_date : true,
    transaction_number : true,
    billing_code : true,
    amount : true,
    loa_number : true,
    atp_number : true
  })
  
  const handleStatusChange = (event) => {
    setSelectedStatus(event.target.value)
  }

  const handleDisplayColumnsChange = (event) => {
    setDisplayColumns({ ...displayColumns, [event.target.value]: event.target.checked });
  }

  const handleDisplayTypeChange = (event) => {
    setSelectedDisplayType(event.target.value)
  }

  const handleRangeChange = (key, value) => {
    setValues(prevState => ({
      ...prevState,
      [key]: value
    }))
  }

  const onClickPopover = event => {
    setAnchorEl(event.currentTarget)
  }

  const onClickApply = () => {
    let fdate = new Date(values.start)
    let tdate = new Date(values.end)
    let endOfToday = moment().endOf('day').toISOString()
    let today = new Date(endOfToday)
    setFromDateError('')
    setToDateError('')
    let error = true
    if (!fdate.valueOf()) {
      setFromDateError(`${startDateError}`)
    } else if (fdate.valueOf() > today.valueOf()) {
      setFromDateError(`${laterStartDateError}`)
    } else if (tdate.valueOf() && fdate.valueOf() && fdate.valueOf() > tdate.valueOf()) {
      setFromDateError(`${startDateGreaterThanEndDateError}`)
    } else {
      error = false
    }

    if (!tdate.valueOf()) {
      setToDateError(`${endDateError}`)
      error = true
    } else if (from !== "AccountListPage" && tdate.valueOf() > today.valueOf()) {
      setToDateError(`${laterEndDateError}`)
      error = true
    }

    if (!error) {
      if (from === 'AccountListPage') {
        handleOnDisplaySettingsApplyClicked({ range: values, status: selectedStatus })
      } else if (from === 'ClientTransactionJournalPage') {
        handleOnDisplaySettingsApplyClicked({ 
          range: values,
          status: selectedStatus,
          displayType: selectedDisplayType,
          displayColumns: displayColumns
        })
      }
    }
  }

  const handleOnCancel = (event) => {
    setFromDateError('')
    setToDateError('')
    setValues(i => ({ ...i, start: null, end: null }))
    handleDisplaySettingsClicked()
  }

  const handleClick = event => {
    if (event.target.value === 'x') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }

  return (
    <React.Fragment>
      <Button id='generate-report' onClick={onClickPopover}>
        <FormatListBulletedIcon className={classes['actions-icon']} fontSize='small' />
        <Typography variant='caption' className={`${classes['actions-label']} bold text-link`}>
          Display Settings
        </Typography>
      </Button>
      <ThemeProvider theme={theme}>
        <Popover
          open={anchorEl}
          anchorEl={anchorEl}
          onClose={() => {
            setToDateError('')
            setFromDateError('')
            setAnchorEl(null)
          }}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right'
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right'
          }}
          PaperProps={{ style: { width: '30%' } }}
        >
          <Grid container style={{ padding: '1rem' }}>
            <Grid item xs xs={10} style={{ margin: '1rem 0rem' }}>
              <Typography variant='subtitle2'>{displaySettingTitle}</Typography>
            </Grid>
            <Grid
              item
              xs={2}
              style={{ 'font-size': '22px', 'padding-left': '20px', 'padding-right': '3px', cursor: 'pointer' }}
              onClick={e => handleClick(e)}
            >
              X
            </Grid>
            <Grid container spacing={2}>
              <Grid item lg={6} style={{ width: '50%' }}>
                <DatePicker
                  placeholder='From'
                  value={values.start}
                  onClick={() => {
                    setToDateError('')
                    setFromDateError('')
                  }}
                  onChange={e =>
                    handleRangeChange(
                      'start',
                      moment(e)
                        .startOf('day')
                        .toISOString()
                    )
                  }
                  maxDateMessage={null}
                />
              </Grid>
              <Grid item lg={6} style={{ width: '50%' }}>
                <DatePicker
                  placeholder='To'
                  value={values.end}
                  onClick={() => {
                    setToDateError('')
                    setFromDateError('')
                  }}
                  onChange={e =>
                    handleRangeChange(
                      'end',
                      moment(e)
                        .endOf('day')
                        .toISOString()
                    )
                  }
                  minDateMessage={null}
                />
              </Grid>
            </Grid>
            <Grid container spacing={3}>
              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                {fromDateError}
              </Grid>
              <Grid item xs={6} style={{ color: 'red', 'font-size': '10px' }}>
                {toDateError}
              </Grid>
            </Grid>
            { from === 'AccountListPage' ?
             <Grid container spacing={3} style={{ marginTop: '5px' }}>
              <Grid item xs xs={12}>
                <Typography variant='subtitle2'>Display Accounts With Contracts That Are:</Typography>
              </Grid>
              <Grid item xs={12} style={{ textAlign: 'center', paddingTop: '0' }}>
                <FormControl component='fieldset'>
                  <RadioGroup row name='display-settings-status' value={selectedStatus} onChange={handleStatusChange}>
                    <FormControlLabel value='active' 
                      control={<Radio color='primary' size='small' />} 
                      label={<Typography variant='subtitle2'>Active</Typography>} 
                    />
                    <FormControlLabel value='inactive' 
                      control={<Radio color='primary' size='small' />} 
                      label={<Typography variant='subtitle2'>Inactive</Typography>} style={{ padding: '0 20px' }} 
                    />
                    <FormControlLabel value='all' 
                      control={<Radio color='primary' size='small' />} 
                      label={<Typography variant='subtitle2'>All Status</Typography>} 
                    />
                  </RadioGroup>
                </FormControl>
              </Grid>
            </Grid> : null }
            { from === 'ClientTransactionJournalPage' ?
            <React.Fragment>
              <Grid container spacing={3} style={{ marginTop: '5px' }}>
                <Grid item xs={6}>
                  <Typography variant='subtitle2' style={{ marginBottom: '5px' }}>Display Type</Typography>
                  <FormControl component='fieldset'>
                    <RadioGroup row name='display-settings-status' value={selectedDisplayType} onChange={handleDisplayTypeChange}>
                      <FormControlLabel value='scrollable' style={{ height: '25px' }}
                        control={<Radio color='primary' size='small' />} 
                        label={<Typography variant='subtitle2'>Scrollable</Typography>}
                      />
                      <FormControlLabel value='compact' style={{ height: '25px' }}
                        control={<Radio color='primary' size='small' />} 
                        label={<Typography variant='subtitle2'>Compact</Typography>} 
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={6}>
                <Typography variant='subtitle2' style={{ marginBottom: '5px' }}>Record Status Display</Typography>
                  <FormControl component='fieldset'>
                    <RadioGroup row name='display-settings-status' value={selectedStatus} onChange={handleStatusChange}>
                      <FormControlLabel value='active' style={{ height: '25px', width: '100%' }}
                        control={<Radio color='primary' size='small' />} 
                        label={<Typography variant='subtitle2'>Active</Typography>} 
                      />
                      <FormControlLabel value='inactive' style={{ height: '25px' }}
                        control={<Radio color='primary' size='small' />} 
                        label={<Typography variant='subtitle2'>Inactive</Typography>} 
                      />
                      <FormControlLabel value='all' style={{ height: '25px' }}
                        control={<Radio color='primary' size='small' />} 
                        label={<Typography variant='subtitle2'>All Status</Typography>} 
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
              </Grid>
              <Grid item xs xs={12}>
                <Typography variant='subtitle2' style={{ marginBottom: '5px' }}>Display Columns</Typography>
              </Grid>
              <Grid container spacing={3}>
                <Grid item xs={6}>
                  <FormControl component="fieldset" className={classes.formControl}>
                    <FormGroup>
                      <FormControlLabel value='billing_code' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.billing_code} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Billing Code</Typography>}
                      />
                      <FormControlLabel value='member_name' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.member_name} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Member Name</Typography>}
                      />
                      <FormControlLabel value='member_type' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.member_type} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Member Type</Typography>}
                      />
                      <FormControlLabel value='effectivity' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.effectivity} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Effectivity Date</Typography>}
                      />
                      <FormControlLabel value='ticket_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.ticket_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Ticket No.</Typography>}
                      />
                      <FormControlLabel value='transaction_status' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.transaction_status} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Transaction Status</Typography>}
                      />
                      <FormControlLabel value='billing_type' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.billing_type} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Billing Type</Typography>}
                      />
                      <FormControlLabel value='soa_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.soa_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>SOA No.</Typography>}
                      />
                      <FormControlLabel value='claim_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.claim_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Claim No.</Typography>}
                      />
                    </FormGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={6}>
                <FormControl component="fieldset" className={classes.formControl}>
                    <FormGroup>
                    <FormControlLabel value='transaction_date' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.transaction_date} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Transaction Date</Typography>}
                      />
                      <FormControlLabel value='member_id' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.member_id} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Member ID</Typography>}
                      />
                      <FormControlLabel value='plan_type' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.plan_type} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Plan Type</Typography>}
                      />
                      <FormControlLabel value='cancelled_date' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.cancelled_date} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Cancelled Date</Typography>}
                      />
                      <FormControlLabel value='transaction_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.transaction_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Transaction No.</Typography>}
                      />
                      <FormControlLabel value='billing_code' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.billing_code} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Billing Code</Typography>}
                      />
                      <FormControlLabel value='amount' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.amount} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>Amount</Typography>}
                      />
                      <FormControlLabel value='loa_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.loa_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>LOA No.</Typography>}
                      />
                      <FormControlLabel value='atp_number' style={{ height: '25px' }}
                        control={<Checkbox checked={displayColumns.atp_number} onChange={handleDisplayColumnsChange} color='primary' size='small'/>}
                        label={<Typography variant='subtitle2'>ATP No.</Typography>}
                      />
                    </FormGroup>
                  </FormControl>
                </Grid>
              </Grid>
            </React.Fragment>
           : null }
            <Grid container direction='row' justify='flex-end' style={{ marginTop: '2rem' }}>
              <Button
                id='display-settings-cancel'
                onClick={handleOnCancel}
                variant='contained'
                disableElevation
                style={
                  {
                    marginRight: '10px',
                    backgroundColor: 'transparent',
                    border: 'solid 1px #3ab77d',
                    color: '#3ab77d',
                    textTransform: 'capitalize',
                    padding: '5px 30px'
                  }
                }
              >
                Cancel
              </Button>
              <Button
                id='display-settings-apply'
                onClick={onClickApply}
                variant='contained'
                className={classes['button-apply']}
                disableElevation
                style={
                  {
                    backgroundColor: '#3ab77d',
                    border: 'solid 1px #3ab77d',
                    color: '#fff',
                    textTransform: 'capitalize',
                    padding: '5px 30px'
                  }
                }
              >
                Apply
              </Button>
            </Grid>
          </Grid>
        </Popover>
      </ThemeProvider>
    </React.Fragment>
  )
}
